import Authorization from "../user/auth.service";

const RolesService = {
    getList() {
        try {
            return Authorization.checkLogin(`/clients/roles`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            name: item.name,
                            id: item.id
                        })
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.name
                })
            }
            return result
        })

    },
}

export default RolesService