<template>
   <section>
      <div class="text-white">
         <div v-if="error_message" class="flex bg-red rounded-xl w-full text-left text-4xl p-4 my-3">
               <i class="fa fa-times-circle"></i>
               <h5 class="text-xl self-center px-2">
               Error enviando informacion. No se pudo crear el nuevo usuario
               <p>{{error_message_text}}</p>
               </h5>
         </div>
         <div v-if="success_message" class="flex bg-green-500 rounded-xl w-full text-left text-4xl p-4 my-3">
               <i class="fa fa-check-circle"></i>
               <h5 class="text-xl self-center px-2">La información ha sido enviada satisfactoriamente.</h5>
         </div>
      </div>               

      <crud-client :info="objId" :mode="modeAction" v-if="this.menuList['client']" v-on:actionInfo="messageInfo" />                
   </section>
</template>
<script>
import CrudClient from "./CrudClient.vue";
export default {
   name: 'Dashboard',
   props: {
      menu: {
         default: 'client'
      },
      modeAction: {
         default: 'create'
      },
      objId: {
         default: 0
      },
   },
   components: {
      CrudClient,          
   },
   data() {
      return {
         error_message_text: '',
         error_message: false,
         success_message: false,
         menuList: {
            client: true,                    
         },
      }
   },
   created() {
      this.updActiveMenu(this.menu)
   },
   computed: {   
   },  
   methods: {     
      updActiveMenu(view) {
         const keys  = Object.keys(this.menuList)               
         keys.map(item => {
            this.menuList[item] = false
         })
         this.menuList[view] = true         
      },
      messageInfo(info){
         if(info.type == 'error') {
         this.error_message_text = info.text
         this.error_message = true
         this.success_message = false
         } else {
         this.error_message = false
         this.success_message = true
         }
         setTimeout(() => {
         this.error_message = false
         this.success_message = false
         }, 3000);
      }, 
   }
}
</script>
